@use 'base/mixins' as m;
@use 'base/variables' as v;

@media (prefers-color-scheme: dark) {
    $grisOscuro: #2c2c3b;

    body {
        background-color: $grisOscuro;
    }

    .tagline, 
    .formulario label,
    .login .acciones a, 
    .crear .acciones a,
    .olvide .acciones a{
        color: v.$blanco;
    }

    .descripcion-pagina,
    .formulario .campo input, 
    .nombre-pagina {
        color: v.$grisClaro;
    }

    .formulario .campo input {
        background-color: lighten($color: $grisOscuro, $amount: 15%);

        &::placeholder {
            color: darken($color: v.$grisClaro, $amount: 20%)
        }
    }

    .alerta.error {
        background-color: v.$blanco;
    }

    .sidebar {
        @include m.tablet {
            background-color: darken($color: $grisOscuro, $amount: 5%);
        }

        .sidebar-nav a {
            &.activo, &:hover {
                background-color: darken($color: v.$naranja, $amount: 5%);
            }
        }
    }

    .barra-mobile, 
    .barra {
        background-color: darken($color: $grisOscuro, $amount: 7%);
    }

    .barra {

        p {
            color: v.$naranja ;

            span {
                color: v.$grisClaro;
            }
        }

        .cerrar-sesion {
            @include m.boton(v.$naranja, v.$blanco);
            padding: 1rem;
            font-size: 1.2rem;
        }
    }

    .filtros {
        background-color: lighten($color: $grisOscuro, $amount: 10%);

        h2, label {
            color: v.$blanco;
        }

        input[type = "radio"] {
            accent-color: v.$naranja;
        }
    }

    .dashboard {
        background-color: $grisOscuro;

        .enlace {
            color: v.$grisClaro;

            &:hover {
                color: darken($color: v.$grisClaro, $amount: 20%);
            }
        }

        .formulario {
            input:not([type = "submit"]) {
                background-color: lighten($color: $grisOscuro, $amount: 20%);
                color: v.$blanco;
    
                &::placeholder {
                    color: darken(v.$grisClaro, 30%);
                }
            }
        }
    }

    .listado-proyectos .proyecto {
        background-color: lighten($color: $grisOscuro, $amount: 20%);

        &:hover {
            background-color: lighten($color: $grisOscuro, $amount: 10%);
        }
    }

    .listado-tareas .tarea {
        background-color: lighten($color: $grisOscuro, $amount: 10%);
        border: none;

        p {
            color: v.$grisClaro;
        }
    }


    .modal .formulario {
        background-color: $grisOscuro;

        input:not([type="submit"]) {
            background-color: lighten($color: $grisOscuro, $amount: 20%);
            border: none;
            color: v.$blanco;

            &::placeholder {
                color: darken(v.$grisClaro, 20%);
            }
        }
        legend {
            color: v.$grisClaro;
        }
    }

    .listado-tareas li.no-tareas {
        color: v.$blanco;
    }
}