@use 'base/variables' as v;
@use 'base/mixins' as m; 

.mensaje {
    margin-top: 5rem;

    .uptask {
       @include m.textoDegradado(v.$naranja);
    }

    .acciones {
        
        justify-content: center;
        a {
            color: darken(v.$naranja, 10%);
        }
    }
}